@use "vars" as *;

/* ====================================================
HTML
==================================================== */
html {
    font-size: 62.5%;
	//font-size: 100%;//16px
	//@include mq(md-min){
	//   font-size: 62.5%;
	//}
}

body {
	color: #233F5E;
	@include font-fm($font-base, 400); 
	font-size: 100%;
	-webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    line-height: 1;
	background: #fff;
	overflow-x: hidden;
}

a {
	display: inline-block;
	color: inherit;
    text-decoration: none;
    outline: none;
	&:hover{
		opacity: 0.7;
	}
	@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
		display: inline-block;
	}
	&[href^="tel"] {
		@include mq(md-min){
			pointer-events: none;
		}
	}
}

table {
	border-collapse: collapse;
	border-spacing: 0;
}
